html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  line-height: 1.2;
  font-weight: 300;
}

p {
  color: #000;
  line-height: 1.8;
}

[class*=__contenedor] {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

[class*=__titulo] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
  width: 100%;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.4rem;
}

h1, h2 {
  font-weight: 400;
}

h3 {
  font-size: 2.4rem;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

.header {
  padding: 5rem 0;
}
.header__titulo {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  display: block;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.barra__contenedor {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .barra__contenedor {
    flex-direction: row;
    align-items: center;
  }
}
.barra__logo {
  margin: 0 auto;
  width: 30rem;
}
@media (min-width: 768px) {
  .barra__logo {
    margin: 0;
  }
}

.menu {
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .menu {
    margin: 0;
  }
}
.menu ul {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .menu ul {
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
  }
}
.menu__element {
  color: #000;
  text-align: center;
  font-size: 2.4rem;
  transition: 0.3 all;
}
.menu__element:hover {
  color: #f46669;
}

.avif .bloque-info__img--izq {
  background-image: url("../img/bloque_1.avif");
}

.noavif.webp .bloque-info__img--izq {
  background-image: url("../img/bloque_1.webp");
}

.noavif.nowebp .bloque-info__img--izq {
  background-image: url("../img/bloque_1.jpg");
}

.avif .bloque-info__img--der {
  background-image: url("../img/bloque_2.avif");
}

.noavif.webp .bloque-info__img--der {
  background-image: url("../img/bloque_2.webp");
}

.noavif.nowebp .bloque-info__img--der {
  background-image: url("../img/bloque_2.jpg");
}

.hero {
  margin-top: 5rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 0;
}
@media (min-width: 768px) {
  .hero {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0;
  }
}

.bloque-info {
  height: 40rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.bloque-info__contenido {
  max-width: 60rem;
  padding: 5rem;
}
.bloque-info__border {
  max-width: 40rem;
  border-block: 1px solid #FFF;
}
.bloque-info--rosa {
  background-color: #f46669;
}
@media (min-width: 768px) {
  .bloque-info--rosa {
    justify-content: flex-end;
  }
}
.bloque-info--verde {
  background-color: #c7c57d;
}
@media (min-width: 768px) {
  .bloque-info--verde {
    grid-column: 2/3;
    justify-content: flex-start;
  }
}
.bloque-info__titulo, .bloque-info__p {
  text-align: center;
  color: #FFF;
}
.bloque-info__img {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
}
@media (min-width: 768px) {
  .bloque-info__img--der {
    grid-column: 1/2;
    grid-row: 2/3;
  }
}

.estudiar {
  padding-bottom: 5rem;
}
.estudiar__titulo {
  text-align: center;
}
.estudiar__titulo::after {
  content: "";
  display: block;
  width: 100%;
  height: 4rem;
  background-image: url("../img/separador.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  margin-top: 3rem;
}
.estudiar__cards {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .estudiar__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.estudiar__img {
  width: auto;
  margin: 0 auto;
}
.estudiar__title {
  font-weight: 300;
  text-align: center;
  margin: 2rem 0;
}
.estudiar__p {
  text-align: center;
}

.clases {
  padding: 5rem 0;
  background-color: #f5f5f5;
}
.clases__titulo {
  text-align: center;
  text-transform: capitalize;
}
.clases__titulo:after {
  content: "";
  display: block;
  width: 100%;
  height: 4rem;
  background-image: url("../img/separador.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  margin-top: 3rem;
}
.clases__grid {
  margin-top: 5rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 3rem;
}
@media (min-width: 768px) {
  .clases__grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .clases__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.clases-card {
  border: 1px solid #dcdcdc;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.clases-card__info {
  display: flex;
  flex-direction: column;
  flex: 1;
}
.clases-card__head {
  background-color: #f46669;
  padding: 1.5rem 1rem;
  display: flex;
  justify-content: space-between;
}
.clases-card__head p {
  margin: 0;
}
.clases-card__fecha {
  color: #FFF;
}
.clases-card__precio {
  background-color: #c7c57d;
  padding: 0.5rem;
  border-radius: 30%;
  font-size: 1.2rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
}
.clases-card__main {
  padding: 2rem;
  background-color: #FFF;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.clases-card__title {
  font-weight: 300;
  margin: 0;
}
.clases-card__p--rojo {
  color: #f46669;
}
.clases-card__btn {
  background-color: #f46669;
  font-size: 2.2rem;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFF;
  text-decoration: none;
  border: none;
  margin-top: 2rem;
  padding: 2rem 4rem;
  text-transform: capitalize;
  font-weight: 700;
  transition: 0.3s background-color;
}
.clases-card__btn:hover {
  cursor: pointer;
  background-color: rgb(219.6, 91.8, 94.5);
}
.clases-card__btn {
  padding: 1rem 0;
  font-size: 1.6rem;
  margin-top: auto;
}

.avif .licenciatura {
  background-image: url("../img/chef_bg.avif");
}

.noavif.webp .licenciatura {
  background-image: url("../img/chef_bg.webp");
}

.noavif.nowebp .licenciatura {
  background-image: url("../img/chef_bg.jpg");
}

.licenciatura {
  height: 65rem;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.licenciatura__contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.licenciatura {
  text-align: center;
}
.licenciatura__titulo {
  color: #FFF;
  text-align: center;
  font-weight: 700;
  margin: 2rem 0 0;
}
.licenciatura__p {
  color: #FFF;
  font-size: 2.6rem;
  max-width: 50rem;
  margin: 2rem 0;
}
.licenciatura__btn {
  background-color: #f46669;
  font-size: 2.2rem;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFF;
  text-decoration: none;
  border: none;
  margin-top: 2rem;
  padding: 2rem 4rem;
  text-transform: capitalize;
  font-weight: 700;
  transition: 0.3s background-color;
}
.licenciatura__btn:hover {
  cursor: pointer;
  background-color: rgb(219.6, 91.8, 94.5);
}
.licenciatura__btn {
  width: fit-content;
  padding: 1.5rem 4rem;
  font-size: 2rem;
  margin-top: 0rem;
}

.footer {
  padding: 3rem 0;
  border-bottom: 0.5rem solid #f46669;
}
.footer__contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .footer__contenedor {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer-menu ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .footer-menu ul {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer-menu__element {
  color: #000;
  font-size: 2.4rem;
  transition: 0.3s all;
}
.footer-menu__element:hover {
  color: #f46669;
}
.footer__p {
  font-size: 2.4rem;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__p {
    margin: 0;
  }
}